<template>
  <div id="app">
    <headerinput :currentZone="currentZone" :locations="locations" :SearchZone.sync="SearchZone" @changeZone="changeZone"></headerinput>
    <travel-content :items="filterData" :currentPage="currentPage" @changePage="changePage" @hotarea="hotarea"></travel-content>
    <footer-content></footer-content>
  </div>
</template>

<script>
import headerinput from './components/header'
import travelContent from './components/travel-content'
import footerContent from './components/footer'
export default {
  name: 'App',
  data(){
    return{
      items:[],
      currentPage:0,
      locations:[],
      currentZone:'',
      SearchZone:'',
    }
  },
  computed:{
    filterData(){
      let data = [];
      if(this.currentZone){
        this.currentPage = 0;
        data = this.items.filter((item)=>{
        return item.Zone ===  this.currentZone
        })
      }else{
        this.currentPage = 0;
        data = this.items
      }
      if (this.SearchZone){
        data = data.filter((item)=>{   
            return  item.Name.indexOf(this.SearchZone) >= 0
        })
      }
      const pageData= [];
      data.forEach((item,idx)=>{
        if(idx % 9 ===0){
          pageData.push([])
        }
        const page = parseInt(idx/9);
        pageData[page].push(item);
      })
      return pageData
    },
  },
  components: {
    headerinput,
    travelContent,
    footerContent
  },
  methods:{
    getdata(){
      var vm = this;
      this.axios.get('https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97')
      .then(function (response) {
        console.log(response.data.result.records);
        vm.items =response.data.result.records;
        vm.getZone()
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    changePage(num){
      this.currentPage = num;
    },
    getZone(){
      const locations = new Set();
      this.items.forEach((item)=>{
        locations.add(item.Zone)
      })
      this.locations = Array.from(locations)
    },
    changeZone(zone){
      this.currentZone = zone ;
    },
    hotarea(hotName){
      this.currentZone = hotName;
    }
  },
  created(){
    this.getdata()
  }
}
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";
#app {
  font-family: '微軟正黑體';
}
</style>
