<template>
  <header class="container-fluid" :style="{backgroundImage:`url(${bg2})`}">
    <div class="container">
      <h1>Kaohsiung travel</h1>
      <div id="mainselection">
        <select @input="changeZone" v-model="current" >
          <option value=''>--- 請選擇行政區域 ---</option>
          <option v-for="location in locations" :key="location" :value="location">{{location}}</option>
        </select>
        <div class="form-inline d-flex mt-2" >
          <input class="form-control mr-sm-2 w-50" type="search" placeholder="地名搜尋" aria-label="Search" @keyup.enter="Search_Zone" v-model="Search_test">
          <button class="btn btn-primary my-2 my-sm-0 w-25" type="submit"  @click.prevent="Search_Zone">搜尋</button>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import bg2 from '../images/header-kv.png';
export default {
  name: 'headerinput',
  props:['locations','currentZone','SearchZone'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      bg2,
      current:this.currentZone,
      Search_test:this.SearchZone
    }
  },
  methods:{
    changeZone(e){
      this.$emit('changeZone',e.target.value)
    },
    Search_Zone(e){
      console.log(this.Search_test)
      this.$emit('update:SearchZone',this.Search_test)
    }
  }
}
</script>

